* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

button {
  cursor: pointer;
  padding: 8px 12px;
}

.title {
  text-align: center;
  color: #10162f;
}

.tasks {
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem 1rem;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.tasks li a {
  color: #10162f;
  background-color: #ffd300;
  border-radius: .5rem;
  padding: .625rem 2rem;
  font-weight: 700;
  text-decoration: none;
  transition: background-color .2s ease-in;
}

.tasks li a:hover {
  background-color: #ffe20d;
}

.back-button {
  color: #10162f;
  width: fit-content;
  background-color: #ffd300;
  border-radius: .5rem;
  align-items: center;
  padding: .5rem 2rem .5rem 1.5rem;
  font-weight: 700;
  text-decoration: none;
  transition: background-color .2s ease-in;
  display: flex;
}

.back-button:hover {
  background-color: #ffe20d;
}

.color-switcher-btn {
  justify-content: center;
  gap: 1rem;
  display: flex;
}

button[data-start], button[data-stop] {
  color: #000;
  background-color: rgba(255, 255, 255, .1);
  border: .125rem solid #000;
  border-radius: .5rem;
  padding: 1rem 2rem;
  font-size: 1rem;
  font-weight: 700;
}

button[data-start]:not([disabled]):hover, button[data-stop]:not([disabled]):hover {
  background-color: rgba(255, 255, 255, .2);
}

button[data-start]:disabled, button[data-stop]:disabled {
  color: rgba(0, 0, 0, .5);
  cursor: not-allowed;
  border: .125rem solid rgba(0, 0, 0, .5);
}

